.background-Color-Main {
  width: 100vw;
  height: 91vh;
  background-color: #0c1e25;
  padding-top: 20px;
}
.backgroun-Color-secondary {
  width: 100vw;
  height: 91vh;
  background-color: #fff;
  padding-top: 20px;
}
.pricing-container {
  display: flex;
  color: #fff;
}
.Pricing-option1,
.Pricing-option2,
.Pricing-option3 {
  width: 30vw;
  height: 80vh;
  background-color: #000d13;
  margin-right: 20px;
  border-radius: 16px;
}
.light-Theme1,
.light-Theme2,
.light-Theme3 {
  width: 30vw;
  height: 80vh;
  background-color: #29534e;
  margin-right: 20px;
  border-radius: 16px;
}
.Pricing-option1 h3,
.Pricing-option2 h3,
.Pricing-option3 h3 {
  display: flex;
  justify-content: center;
  font-size: 2.3rem;
  color: #ee9b00;
}
.Price {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
}
.light-Theme1 h3,
.light-Theme2 h3,
.light-Theme3 h3 {
  font-size: 2.3rem;
  display: flex;
  justify-content: center;
  color: #fff;
}
.Pricing-option1 h2,
.Pricing-option2 h2,
.Pricing-option3 h2 {
  font-size: 3rem;
  margin-bottom: 12px;
  display: flex;
  justify-content: center;
}
.light-Theme1 h2,
.light-Theme2 h2,
.light-Theme3 h2 {
  color: #ff206e;
  font-size: 3rem;
  margin-bottom: 12px;
  display: flex;
  justify-content: center;
}
.Pricing-option1 span,
.Pricing-option2 span,
.Pricing-option3 span {
  display: flex;
  font-size: 1rem;
  margin-left: 50px;
  margin-bottom: 8px;
  font-weight: 300px;
}
#MonthlyYearly {
  font-size: 1.4rem;
}
.light-Theme1 span,
.light-Theme2 span,
.light-Theme3 span {
  display: flex;
  font-size: 1rem;
  margin-left: 50px;
  margin-bottom: 8px;
  font-weight: 300px;
  color: #fff;
}
#checkmark {
  margin-right: 9px;
}
button {
  width: 300px;
  height: 6vh;
  margin-left: 55px;
  border: none;
  border-radius: 7px;
  background-color: #659664;
  color: #fff;
  font-size: 1.3rem;
  cursor: pointer;
}
button:hover {
  background-color: #588157;
  transition: 0.4s;
}
#button1 {
  margin-top: 70px;
}
#button2 {
  margin-top: 35px;
}
#button3 {
  margin-top: 4px;
}
#darktheme {
  display: flex;
  justify-content: flex-end;
}
.Pricing-yearly-darktheme {
  position: relative;
  margin-left: 250px;
  width: 70px;
  height: 40px;
  border-radius: 20px;
  background-color: #413e44;
  margin-right: 10px;
}
.Pricing-monthly-darktheme {
  position: absolute;
  left: 5px;
  top: 4px;
  background-color: #fff;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  transition: 0.6s;
}
.Pricing-monthly-light {
  position: absolute;
  background-color: #fff;
  left: 32px;
  top: 4px;
  width: 33px;
  height: 33px;
  border-radius: 50%;
  transition: 0.6s;
}
